import axios from 'axios';

const http = axios.create({
    baseURL: 'http://localhost:3001',
    headers: {
        Accept: 'application/json',
        Content: 'application.json'
    }
})

http.interceptors.request.use(function (config) {
    // Faz alguma coisa antes da requisio ser enviada
    const token = localStorage.getItem('Authorization')
    if(token && config.headers) {
        config.headers.Authorization = `Bearer ${token}`
    }
    return config;
}, function (error) {
    // Faz alguma coisa com o erro da requisio
    return Promise.reject(error);
});

http.interceptors.response.use(function (response) {
    return response
}, async function (error) {
    console.log(error)
    const originalConfig = error.config;
    if (originalConfig.url !== "/login" && error.response) {
        // Access Token was expired
        if (error.response.status === 401 && !originalConfig._retry) {
            originalConfig._retry = true;
            try {
                const refresh = localStorage.getItem('RefreshToken')
                const rs = await axios.post("http://localhost:3001/usuarios/atualiza-token", { refreshToken: refresh });              
                const accessToken = rs.headers.authorization
                const { refreshToken } = rs.data
                localStorage.setItem('Authorization', accessToken)
                localStorage.setItem('RefreshToken', refreshToken)
                return http(originalConfig.url);
            } catch (_error) {
                return Promise.reject(_error);
            }
        }
    }
    return Promise.reject(error);
})

export default http