import React, { useRef, useState } from 'react'

import * as S from './style'

import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import axios from 'axios';
import { Button, Input } from '@mui/material';

import http from '../../http'

export default function Laudos() {
    const [codigoAT, setCodigoAT] = useState("")
    const [file, setFile] = useState()
    const [pdf, setPdf] = useState()
    const inputFile = useRef(null)

    const changeHandler = (event) => {
		setFile(event.target.files[0]);
	};

    const onButtonClick = (e) => {
        // `current` points to the mounted file input element
        inputFile.current.click()

    };

    const baixarLaudo = (e) => {
        let link = document.createElement('a')
        link.href = pdf
        link.setAttribute('download', 'laudo.pdf')
        document.body.appendChild(link)
        link.click()
    }

    const handleChangeCodigoAT = (event) => {
        setCodigoAT(event.target.value)
    }

    const sendLaudoEmail = (event) => {
        event.preventDefault();
        const idLaudo = {
            codigoAT
        }
        const token = localStorage.getItem('Authorization')
        const config = {
            headers: { Authorization: `Bearer ${token}` },
            responseType: 'blob'
        };
        http.post('/laudoss', idLaudo, config)
            .then(res => {
                if (res.status === 200) {
                    alert('email enviado!')

                } else {

                }
            })
            .catch(err => {
                console.log(err.message)
                if (err?.message) {
                    alert(err.message)
                    setPdf("")
                    setCodigoAT("")
                } else {
                    alert('Aconteceu um erro inesperado ao efetuar o seu login.')
                    setPdf("")
                    setCodigoAT("")
                }
            })
    }

    const aoSubmeterForm = event => {
        event.preventDefault();
        const idLaudo = {
            codigoAT
        }
        const token = localStorage.getItem('Authorization')
        const config = {
            headers: { Authorization: `Bearer ${token}` },
            responseType: 'blob'
        };
        axios.post(`http://localhost:3001/laudos`, idLaudo, config)
            .then(res => {
                if (res.status === 200) {
                    const href = URL.createObjectURL(res.data);
                    setPdf(href)
                } else {
                    alert(res.data)
                }
            })
            .catch(err => {
                if (err?.message) {
                    alert(err.message)
                    setPdf("")
                    setCodigoAT("")
                } else {
                    alert('Aconteceu um erro inesperado ao efetuar o seu login.')
                    setPdf("")
                    setCodigoAT("")
                }
            })
    }

    

    return (
        <Paper>
            <S.ContainerLaudos pdf={pdf}>
                <S.TitleLaudos>Enviar um novo laudo</S.TitleLaudos>
                <S.ContainerInputsLaudo>
                    <S.FormularioBuscaLaudo method="get" onSubmit={aoSubmeterForm}>
                        <S.formTitle>Cdigo AT:</S.formTitle>
                        <S.formInput id="standard-basic" label="Cdigo AT*" variant="outlined" value={codigoAT} onChange={handleChangeCodigoAT}/>
                        <Button variant="contained" type="submit">Buscar laudo</Button>
                    </S.FormularioBuscaLaudo>
                </S.ContainerInputsLaudo>
                
                <iframe src={pdf} width="100%" height="100%" />
                <>

                    
                    {pdf && (
                        <S.ContainerButtonsHandleLaudo>
                            <input type="file" name="laudo" ref={inputFile} accept="application/pdf" onChange={changeHandler} style={{ display: 'none' }} />
                            
                            <Button variant="contained" type="submit" onClick={onButtonClick}>
                                Atualizar laudo
                            </Button>
                            <Button variant="contained" type="submit" onClick={baixarLaudo}>Baixar laudo</Button>
                        </S.ContainerButtonsHandleLaudo>
                    )}


                    <Button variant="contained" type="submit" onClick={sendLaudoEmail}>
                        Enviar laudo por e-mail       
                    </Button>
                </>

                
                {/* <S.ContainerInputsLaudo>
                    <S.formTitle>Escolher arquivo (somente .pdf):</S.formTitle>
                    <form onSubmit={onSubmit}>
                        <input type="file" name="laudo" accept="application/pdf" onChange={changeHandler}/>
                        <button>Submit</button>
                    </form>
                </S.ContainerInputsLaudo> */}
            
            </S.ContainerLaudos>
        </Paper>
    )
}