import styled from 'styled-components'
import TextField from '@mui/material/TextField';

export const ContainerLaudos = styled.div`
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 90px ${props => {
        return (props.pdf ? "600px" : "150px")
    }};
    align-items: center;
`
export const TitleLaudos = styled.h3`
    fontsize: 18px;
    padding: 20px;
    background-color: #ededed;
`

export const ContainerInputsLaudo = styled.div`
    background-color: #fbfbfb;
    height: 100%;
    padding: 20px;
`

export const FormularioBuscaLaudo = styled.form`
    display: grid;
    grid-template-columns: 150px 250px 150px;
    grid-template-rows: 1fr;
    gap: 30px;
    align-items: center;
`

export const formTitle = styled.div`

`

export const formInput = styled(TextField)`
    justify-self: start;
`

export const ContainerInputFile = styled.div`
    background-color: #fbfbfb;
    height: 100%;
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: center;
`

export const ContainerButtonsHandleLaudo = styled.div`
    display: flex;
    width: 500px;
    gap: 20px;
    padding: 20px;
`
