import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Toolbar from '@mui/material/Toolbar';

import Divider from '@mui/material/Divider';

import { useState } from 'react';


import logoAbralav from '../../assets/abralav-qdd.svg'

import ListaMenu from '../ListaMenu';

import * as S from './style'

import BasicBreadcrumbs from '../BasicBreadcrumbs'
import NavBar from '../NavBar';
import { Outlet } from 'react-router-dom';
import { useEffect } from 'react';
import AccordionCustomizado from '../AccordionCustomizado';

const drawerWidth = 240;

export default function SideBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = useState(false);


  const [usuarioEstaLogado, setUsuarioEstaLogado] = useState(false)

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <>
      <S.LogoAbralav src={logoAbralav} />
      <Divider />
      <AccordionCustomizado />
    </>
  )

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <NavBar onClick={handleDrawerToggle}/>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
      <Drawer
        variant="temporary"
        container={container}
        open={mobileOpen}
        onClose={handleDrawerToggle}
        onClick={handleDrawerToggle}
        anchor="left"
        ModalProps={{
          keepMounted: true, // Better open performance on mobile.
        }}
        sx={{
          display: { xs: 'block', sm: 'none' },
          '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
        }}
      >
        {drawer}
      </Drawer>
      <Drawer
        variant="permanent"
        sx={{
          display: { xs: 'none', sm: 'block' },
          '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
        }}
        open
      >
        {drawer}
      </Drawer>
      </Box>
      
      <Box
        component="main"
        // sx={{ flexGrow: 1, bgcolor: 'background.default', p: 3 }}
        sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
      >
        <Toolbar />
        {true && (
          <>
            <BasicBreadcrumbs />
            <Outlet />
          </>
        )}
        {/* {!usuarioEstaLogado && (
          <span>Voc precisa estar logado para acessar esta pgina.</span>
        )} */}
      </Box>
    </Box>
  );
}