import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

import http from '../../http'

import * as S from './style'

import Logo from '../../components/Logo'

// import * as S from './style'

export default function Login() {
    const [cnpj, setCnpj] = useState("");
    const [senha, setSenha] = useState("");
    const [displaySideBar, setDisplaySideBar] = useState('true')
    const navigate  = useNavigate()

    const aoSubmeterForm = event => {
        event.preventDefault();
        const user = {
            cnpj,
            senha
        }
        http.post('/usuarios/login', user)
            .then(res => {
                if (res.status === 200) {
                    localStorage.setItem('Authorization', res.headers.authorization)
                    localStorage.setItem('RefreshToken', res.data.refreshToken)
                    navigate('/inicial')
                    setDisplaySideBar('false')
                }
            })
            .catch(err => {
                if (err?.response?.data?.message !== "Cannot destructure property 'refreshToken' of 'rs.body' as it is undefined.") {
                    alert(err.response.data.message)
                } else {
                    alert('Aconteceu um erro inesperado ao efetuar o seu login.')
                }
            })
        setCnpj("");
        setSenha("");
    }

    const handleChangeLogin = (event) => {
        setCnpj(event.target.value)
    }
    const handleChangeSenha = (event) => {
        setSenha(event.target.value)
    }
    return (
        <>
            <S.ContainerPage>
                <S.NavBarLogin sx={{backgroundColor: '#FFF'}}>
                    <S.ContainerLogoTitle>
                        <Logo />
                        <S.NavBarTitle>rea do associado - Login</S.NavBarTitle>
                    </S.ContainerLogoTitle>
                </S.NavBarLogin>
                
                <S.ContainerLogin>
                    <S.Formulario method="post" onSubmit={aoSubmeterForm}>
                        <S.ContainerTextInputs>
                            <S.FormTitle>Usurio*</S.FormTitle>
                            <S.formInput type="text" value={cnpj} onChange={handleChangeLogin} placeholder="CNPJ completo*"/>
                            <S.FormTitle>Senha*</S.FormTitle>
                            <S.formInput type="password" value={senha} onChange={handleChangeSenha} placeholder="Senha*" />
                        </S.ContainerTextInputs>
                        <S.ContainerButtons>
                            <S.LoginButton type="submit" variant="contained" sx={{ margin: '10px'}}>Fazer login</S.LoginButton>
                            <S.RecuperarSenhaButton variant="outlined" sx={{ margin: '10px'}}>Recuperar senha</S.RecuperarSenhaButton>
                        </S.ContainerButtons>
                    </S.Formulario>
                    
                </S.ContainerLogin>
            </S.ContainerPage>
            
        </>
    )
}

