import React, { useEffect, useState } from 'react'
import { ThemeProvider } from 'styled-components'

import Paper from '@mui/material/Paper';

import * as S from './style'
import DataCard from '../../components/DataCard'
import SideBar from '../../components/SideBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import axios from 'axios';
import http from '../../http';


const pegaData = (data) => {
    const day = data.getDate()
    const month = (data.getMonth() + 1)*100
    const year = data.getFullYear()*10000
    return year + month + day
}


export default function Inicial() {
    const [totalUsuarios, setTotalUsuarios] = useState('')
    const [totalUsuariosDoDia, setTotalUsuariosDoDia] = useState('')
    const [totalOcorrencias, setTotalOcorrencias] = useState('')
    const [totalOcorrenciasDoDia, setTotalOcorrenciasDoDia] = useState('')
    
    useEffect(() => {
        http.get('http://localhost:3001/usuarios', )
        .then(res => {
            const data = new Date()
            const hoje = pegaData(data)
            const arrDataCriacao = res.data.map(value => pegaData(new Date(value.createdAt)))
            const arrHoje = arrDataCriacao.filter(value => value === hoje)
            setTotalUsuarios(res.data.length)
            setTotalUsuariosDoDia(arrHoje.length)

        })
        http.get('http://localhost:3001/cadastros', )
        .then(res => {
            const data = new Date()
            const hoje = pegaData(data)
            const arrDataCriacao = res.data.map(value => pegaData(new Date(value.createdAt)))
            const arrHoje = arrDataCriacao.filter(value => value === hoje)
            setTotalOcorrenciasDoDia(arrHoje.length)
            setTotalOcorrencias(res.data.length)
        })

    }, [])

    return (
        <>
            <Toolbar />
            <Paper elevation={1} >
                <S.ContainerCards>
                    <ThemeProvider theme={{ main: "#0088CC" }}>
                        <DataCard titulo={"Ocorrncias hoje"} dado={`${totalOcorrenciasDoDia} ocorrncias`} cor={"#0088CC"} tipo={1} />
                    </ThemeProvider>
                    <ThemeProvider theme={{ main: "#E36159" }}>
                        <DataCard titulo={"Total de ocorrncias"} dado={`${totalOcorrencias} ocorrncias`} cor={"#E36159"} tipo={2} />
                    </ThemeProvider>
                    <ThemeProvider theme={{ main: "#2baab1" }}>
                        <DataCard titulo={"Novos associados hoje"} dado={`${totalUsuariosDoDia} associados`} cor={"#2baab1"} tipo={3} />
                    </ThemeProvider>
                    <ThemeProvider theme={{ main: "#734ba9" }}>
                        <DataCard titulo={"Total de associados"} dado={`${totalUsuarios} associados`} cor={"#734ba9"} tipo={4} />
                    </ThemeProvider>
                </S.ContainerCards>
            </Paper>
        </>
    )
}