import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import InsightsIcon from '@mui/icons-material/Insights';
import PersonIcon from '@mui/icons-material/Person';
import GroupsIcon from '@mui/icons-material/Groups';
import { Link } from 'react-router-dom';
import * as S from './style'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
    // border: `1px solid ${theme.palette.divider}`,
    // '&:not(:last-child)': {
    //     borderBottom: 0,
    // },
    '&:before': {
        display: 'none',
    },
}));

const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
        expandIcon={<ExpandMoreIcon sx={{ fontSize: '1.5rem' }} />}
        {...props}
    />
))(({ theme }) => ({
    backgroundColor:
        theme.palette.mode === 'dark'
            ? 'rgba(255, 255, 255, .05)'
            : 'rgba(255, 255, 255, .03)',
    flexDirection: 'row',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
        transform: 'rotate(180deg)',
    },
    '&:hover': {
        backgroundColor: 'rgba(0, 0, 0, .03)'
    }
    // '& .MuiAccordionSummary-content': {
    //     marginLeft: theme.spacing(1),
    // },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: 0,
    borderTop: '1px solid rgba(0, 0, 0, .125)',
    borderBottom: '1px solid rgba(0, 0, 0, .125)',
    // backgroundColor: 'rgba(0, 0, 0, .03)'
}));

export default function AccordionCustomizado() {
    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <div>
            <AccordionDetails sx={{ borderTop: '0px',
    borderBottom: '0px',}}>
                <ListItem key="Inicial" disablePadding>
                    <S.ContainerMenuItem to="/inicial">
                        <ListItemButton>
                            <ListItemIcon>
                                <HomeIcon />
                                <Link to="/inicial" />
                            </ListItemIcon>
                            <ListItemText primary="Inicial" />
                        </ListItemButton>
                    </S.ContainerMenuItem>
                </ListItem>
            </AccordionDetails>
            <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header" >
                    <ListItemIcon>
                        <InfoIcon />
                    </ListItemIcon>
                    <Typography>Laudos</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ListItem key="Laudos" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Enviar laudo" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
                    <ListItemIcon>
                    <WarningAmberIcon />
                    </ListItemIcon>
                    <Typography>Ocorrncias</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ListItem key="CadastrarOcorrencia" disablePadding>
                        <S.ContainerMenuItem to="/cadastrar-ocorrencia">
                            <ListItemButton>
                                <ListItemText primary="Cadastrar ocorrncia"  sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="EditarOcorrencia" disablePadding>
                        <S.ContainerMenuItem to="/editar-ocorrencia">
                            <ListItemButton>
                                <ListItemText primary="Editar ocorrncia" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
                <ListItemIcon>
                  <InsightsIcon />
                </ListItemIcon>
                    <Typography>Relatrios</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ListItem key="RelatorioOcorrencia" disablePadding>
                        <S.ContainerMenuItem to="/ocorrencias">
                            <ListItemButton>
                                <ListItemText primary="Dados de ocorrncias"  sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="RelatorioAssociados" disablePadding>
                        <S.ContainerMenuItem to="/dados-associados">
                            <ListItemButton>
                                <ListItemText primary="Dados de associados" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
                    <ListItemIcon>
                    <PersonIcon />
                    </ListItemIcon>
                    <Typography>Permisses</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ListItem key="CadastrarAdministrador" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Cadastrar administrador"  sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="EditarAdministrador" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Editar administrador" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
                <AccordionSummary aria-controls="panel5d-content" id="panel5d-header">
                    <ListItemIcon>
                    <GroupsIcon />
                    </ListItemIcon>
                    <Typography>Associados</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ListItem key="CadastrarAssociados" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Cadastrar administrador"  sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="EditarAssociados" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Editar administrador" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="GerenciarAcesso" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Gerenciar acesso" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                    <ListItem key="LiberarAcesso" disablePadding>
                        <S.ContainerMenuItem to="/info">
                            <ListItemButton>
                                <ListItemText primary="Liberar acesso" sx={{marginLeft: '56px'}}/>
                            </ListItemButton>
                        </S.ContainerMenuItem>
                    </ListItem>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}