import React from 'react'
import * as S from './style'

import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormAutocomplete from '../FormAutocompleteSemChild';

export default function FormTextInputSemChild(props) {

    return (
        <S.ContainerItemCadastro>
            <S.ItemTitle>{props.titulo}</S.ItemTitle>
            <S.ContainerComButton>
                <S.InputText disabled={props.disabled} id="outlined-basic" variant="outlined" value={props.value} size="small" onChange={props.onChange} multiline={props.multiline} maxRows={props.maxRows}
                />
            </S.ContainerComButton>
            
        </S.ContainerItemCadastro>
    )
}