import React from 'react'
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';

import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import * as S from './style'
import http from '../../http'

import FormTextInput from '../../components/FormTextInput';
import FormSelectInput from '../../components/FormSelectInput';
import FormDateInput from '../../components/FormDateInput';
import FormRadioInput from '../../components/FormRadioInput'
import FormSubmitButton from '../../components/FormSubmitButton';
import FormAutocomplete from '../../components/FormAutocompleteSemChild';
import EditItem from '../../components/EditItem/index.js';

import ModalEditItem from '../../components/ModalEditItem';
import FormTextInputSemChild from '../../components/FormTextInputSemChild';
import FormDateInputSemChild from '../../components/FormDateInputSemChild';
import FormRadioInputSemChild from '../../components/FormRadioInputSemChild';
import FormAutocompleteComChild from '../../components/FormAutocompleteComChild';

export default function EditarOcorrencia() {
    
    const [codigoAT, setCodigoAT] = React.useState('')
    const [codigoATId, setCodigoATId] = React.useState('')
    const [inputCodigoAT, setInputCodigoAT] = React.useState('')
    const [inputCodigoATId, setInputCodigoATId] = React.useState('')
    const [itemsCodigoAT, setItemsCodigoAT] = React.useState([])
    const [idUsuario, setIdUsuario] = React.useState('')

    const [associado, setAssociado] = React.useState('');
    const [associadoId, setAssociadoId] = React.useState('');
    const [inputAssociado, setInputAssociado] = React.useState('');
    const [inputAssociadoId, setInputAssociadoId] = React.useState('');
    const [itensAssociado, setItensAssociado] = React.useState([]);

    const [pecas, setPecas] = React.useState('');
    const [pecasId, setPecasId] = React.useState('');
    const [inputPecas, setInputPecas] = React.useState('');
    const [inputPecasId, setInputPecasId] = React.useState('');
    const [itemsPecas, setItemsPecas] = React.useState([]);
    const [itemId, setItemId] = React.useState([]);

    const [instrucaoLimpeza, setInstrucaoLimpeza] = React.useState('');
    const [limpezaUtilizada, setLimpezaUtilizada] = React.useState('');
    const [danoReclamado, setDanoReclamado] = React.useState('');
    const [dataOcorrencia, setDataOcorrencia] = React.useState('');
    const [dataSaida, setDataSaida] = React.useState('');
    const [marca, setMarca] = React.useState('');
    const [marcaId, setMarcaId] = React.useState('');
    
    const [brandId, setBrandId] = React.useState('');

    const [inputMarca, setInputMarca] = React.useState('');
    const [inputMarcaId, setInputMarcaId] = React.useState();
    const [itemsMarca, setItemsMarca] = React.useState([]);
    const [tipoFibra, setTipoFibra] = React.useState('');
    const [foiPassado, setFoiPassado] = React.useState("0");
    const [foiSecado, setFoiSecado] = React.useState("0");
    const [encaminhaSeguro, setEncaminhaSeguro] = React.useState("0");
    const [autorizaTestes, setAutorizaTestes] = React.useState("0");
    const [estado, setEstado] = React.useState('');
    const [cidade, setCidade] = React.useState('');
    const [cidadeId, setCidadeId] = React.useState();

    const [responsabilidade, setResponsabilidade] = React.useState('');
    const [nomeTecnico, setNomeTecnico] = React.useState('');
    const [nomeCliente, setNomeCliente] = React.useState('');
    const [ticket, setTicket] = React.useState('');

    const [solucao, setSolucao] = React.useState('');
    const [observacao, setObservacao] = React.useState('');
    const [encaminha, setEncaminha] = React.useState("0");

    const [open, setOpen] = React.useState(false);
    const [selected, setSelected] = React.useState();
    const [tempValues, setTempValues] = React.useState();

    const navigate  = useNavigate()

    const handleClick = (name, nameId, itemSelecionado, value) => (event) => {
        event.preventDefault()
        value ? setTempValues(value) : setTempValues("")
        setSelected(itemSelecionado)
        name()
        nameId()
        setOpen(true);
    }

    const handleClickFormText = (name, itemSelecionado, value) => (event) => {
        event.preventDefault()
        value ? setTempValues(value) : setTempValues("")
        setSelected(itemSelecionado)
        name()
        setOpen(true);
    }

    const handleClose = (name) => (event) => {
        if(name === 'associado') {
            setAssociado(tempValues)
        }
        if(name === 'pecas') {
            setPecas(tempValues)
        }
        if(name === 'instrucaoLimpeza') {
            setInstrucaoLimpeza(tempValues)
        }
        if(name === 'limpezaUtilizada') {
            setLimpezaUtilizada(tempValues)
        }
        if(name === 'danoReclamado') {
            setDanoReclamado(tempValues)
        }
        if(name === 'dataOcorrencia') {
            setDataOcorrencia(tempValues)
        }
        if(name === 'dataSaida') {
            setDataSaida(tempValues)
        }
        if(name === 'marca') {
            setMarca(tempValues)
        }
        if(name === 'tipoFibra') {
            setTipoFibra(tempValues)
        }
        if(name === 'foiPassado') {
            setFoiPassado(tempValues)
        }
        if(name === 'foiSecado') {
            setFoiSecado(tempValues)
        }
        if(name === 'encaminhaSeguro') {
            setEncaminhaSeguro(tempValues)
        }
        if(name === 'autorizaTestes') {
            setAutorizaTestes(tempValues)
        }
        if(name === 'estado') {
            setEstado(tempValues)
        }
        if(name === 'cidade') {
            setCidade(tempValues)
        }
        if(name === 'responsabilidade') {
            setResponsabilidade(tempValues)
        }
        if(name === 'responsabilidade') {
            setResponsabilidade(tempValues)
        }
        if(name === 'nomeTecnico') {
            setNomeTecnico(tempValues)
        }
        if(name === 'nomeCliente') {
            setNomeCliente(tempValues)
        }
        if(name === 'ticket') {
            setTicket(tempValues)
        }
        if(name === 'solucao') {
            setSolucao(tempValues)
        }
        if(name === 'observacao') {
            setObservacao(tempValues)
        }
        if(name === 'encaminha') {
            setEncaminha(tempValues)
        }

        setTempValues()
        setOpen(false);
    }

    const handleConfirma = () =>  {
        setOpen(false);
    }

    const handleChange = (name) => (event) => {
        name(event.target.value);
    };

    const handleRender = (params) => {
        return <TextField { ...params } />
    }

    const handleChangeAutocompleteValue = (name, nameId, itemId, listaOpcoes) => (event, newValue) => {
        if(itemId || listaOpcoes) {
            const userId = listaOpcoes[event.target.dataset.optionIndex] 
            itemId(userId.id)
        }
        name(newValue);
        nameId(event.target.dataset.optionIndex)        
    }
 
    useEffect(() => {
        // busca lista de cadastros para autocomplete de codigoAT no campo de busca
        http.get(`http://localhost:3001/ocorrencias`)
            .then(res => {
                const objOcorrencias = res.data.map(value => {
                    return {
                        ...value
                    } 
                })
                setItemsCodigoAT(objOcorrencias)
            })
            .catch(err => console.log(err))

        //busca lista de associados 
        http.get(`http://localhost:3001/cadastros`)
            .then(res => {
                console.log('pioujasbdpoci', res)
                const objUsuarios = res.data.map(value => {
                    return {
                        usuarioECnpj: `${value.Usuario.cnpj} - ${value.social_name}`,
                        id: value.id
                    } 
                })

                setItensAssociado(objUsuarios)

            })
            .catch(err => console.log(err))

        // busca lista de peas
        http.get(`http://localhost:3001/items`)
            .then(res => {
                const objItems = res.data.map(value => {
                    return {
                        id: value.id,
                        name: value.name,
                    } 
                })
                setItemsPecas(objItems)
            })
            .catch(err => console.log(err))
        
        // busca lista de marcas
        http.get(`http://localhost:3001/brands`)
            .then(res => {
                const objMarcas = res.data.map(value => {
                    return {
                        id: value.id,
                        name: value.name,
                    } 
                })
                setItemsMarca(objMarcas)
            })
            .catch(err => console.log(err))
    }, [])


    //busca valores com base no codigoAT
    useEffect(() => {
        if(codigoAT) {

            http.get(`http://localhost:3001/ocorrencias/${codigoAT}`)
            .then(res => {
                const resposta = res.data

                setAssociado(`${resposta.Usuario.cnpj} - ${resposta.Usuario.Cadastro.social_name}`)

                setIdUsuario(resposta.Usuario.id)

                setPecas(resposta.Item.name)
                setItemId(resposta.Item.id)

                const instrucaoLimpezaBase = resposta.wash_instruct
                setInstrucaoLimpeza(instrucaoLimpezaBase)

                const limpezaUtilizadaBase = resposta.wash_used
                setLimpezaUtilizada(limpezaUtilizadaBase)     

                const danoReclamadoBase = resposta.damage
                setDanoReclamado(danoReclamadoBase)     

                const dataOcorrenciaBase = resposta.ocurrence_date
                setDataOcorrencia(dataOcorrenciaBase)     

                const dataSaidaBase = resposta.out_date
                setDataSaida(dataSaidaBase)     

                const marcaBase = resposta.Brand
                setMarca(marcaBase.name)     
                setBrandId(marcaBase.id)

                const tipoFibraBase = resposta.fiber_type
                setTipoFibra(tipoFibraBase)     
                
                const foiPassadoBase = resposta.iron_clothes
                setFoiPassado(foiPassadoBase)
                
                const foiSecadoBase = resposta.dry_dryer
                setFoiSecado(foiSecadoBase)
                
                const encaminhadoSeguroBase = resposta.insurance
                setEncaminhaSeguro(encaminhadoSeguroBase)
                
                const autorizatestesBase = resposta.tests
                setAutorizaTestes(autorizatestesBase)
                
                const cidadeBase = resposta.Cidade.name
                const cidadeIdBase = resposta.Cidade.id
                console.log('iojsnadcpojwnsdckasjdnclaksdjcnalskdjcn', resposta)
                setCidade(cidadeBase)
                setCidadeId(cidadeIdBase)

                const estadoBase = resposta.Cidade.Estado.stand
                setEstado(estadoBase)

                const responsabilidadeBase = resposta.responsible
                setResponsabilidade(responsabilidadeBase)

                const nomeTecnicoBase = resposta.technician
                setNomeTecnico(nomeTecnicoBase)

                const nomeClienteBase = resposta.client
                setNomeCliente(nomeClienteBase)

                const ticketBase = resposta.ticket
                setTicket(ticketBase)

                const soluc = resposta.solution
                setSolucao(soluc)
                
                const obs = resposta.observation
                setObservacao(obs)

                const deveEncaminhar = resposta.routing
                setEncaminha(deveEncaminhar)
            })
        }
    }, [codigoAT])

    const salvarAlteracoes = (event) => {
        event.preventDefault()
        const enviaCadastro = {
            codigoAT: Number(codigoAT),
            user: Number(idUsuario),
            item: Number(itemId),
            wash_instruct: instrucaoLimpeza,
            wash_used: limpezaUtilizada,
            ocurrence_date: dataOcorrencia,
            out_date: dataSaida,
            brand: Number(brandId),
            fiber_type: tipoFibra,
            iron_clothes: foiPassado,
            dry_dryer: foiSecado, 
            insurance: encaminhaSeguro,
            tests: autorizaTestes, 
            city: Number(cidadeId),
            responsible: responsabilidade,
            technician: nomeTecnico,
            client: nomeCliente,
            ticket: ticket,
            solution: solucao,
            observation: observacao, 
            routing: encaminha, 
        }

        if(
            !(enviaCadastro.codigoAT) || 
            !(enviaCadastro.user) ||
            !(enviaCadastro.item) ||
            !(enviaCadastro.wash_instruct) ||
            !(enviaCadastro.wash_used) ||
            !(enviaCadastro.damage) ||
            !(enviaCadastro.ocurrence_date) ||
            !(enviaCadastro.brand) ||
            !(enviaCadastro.fiber_type) ||
            !(enviaCadastro.iron_clothes) ||
            !(enviaCadastro.dry_dryer) ||
            !(enviaCadastro.insurance) ||
            !(enviaCadastro.tests) ||
            !(enviaCadastro.client) ||
            !(enviaCadastro.routing)
        ) {
            alert(`Os campos obrigatrios devem ser preenchidos.`)
        }
        else {
            http.put(`/usuarios/${idUsuario}/ocorrencias/${codigoAT}`, enviaCadastro)
            .then(res => {
                alert(res.data.mensagem) 
                navigate(0)
            })
            .catch(err => {
                alert(err.message)
            })
        }

    }

    const enviaCadastroOBJ = {
        codigoAT: Number(codigoAT),
        user: Number(idUsuario),
        item: Number(itemId),
        wash_instruct: instrucaoLimpeza,
        wash_used: limpezaUtilizada,
        damage: danoReclamado,
        ocurrence_date: dataOcorrencia,
        out_date: dataSaida,
        brand: Number(brandId),
        fiber_type: tipoFibra,
        iron_clothes: foiPassado,
        dry_dryer: foiSecado, 
        insurance: encaminhaSeguro,
        testes: autorizaTestes, 
        city: Number(cidadeId),
        responsible: responsabilidade,
        technician: nomeTecnico,
        client: nomeCliente,
        ticket: ticket,
        solution: solucao,
        observation: observacao, 
        routing: encaminha, 
    }


    console.log('enviaCadastroOBJenviaCadastroOBJenviaCadastroOBJ', enviaCadastroOBJ)

    return (
        <S.ContainerMain>
            <Paper>
                <S.TitleLaudos>Editar uma ocorrncia</S.TitleLaudos>
                <S.Formulario>
                    <FormAutocompleteComChild
                            titulo={'Cdigo AT*'}
                            value={codigoAT}
                            onChange={handleChangeAutocompleteValue(setCodigoAT, setCodigoATId)}
                            inputValue={inputCodigoAT}
                            onInputChange={handleChangeAutocompleteValue(setInputCodigoAT, setInputCodigoATId)}
                            options={itemsCodigoAT.map(value => `${value.id}`)}
                            renderInput={handleRender}         
                            size="small"
                        >
                            <></>
                        </FormAutocompleteComChild>
                </S.Formulario>

                {codigoAT && (
                <S.Formulario  method="put" onSubmit={salvarAlteracoes}>
                    <ModalEditItem open={open} onClose={handleClose(selected)} confirma={handleConfirma}>
                        {
                            (selected === 'associado') && (
                                <FormAutocomplete
                                titulo={'Associado'}
                                value={associado}
                                onChange={handleChangeAutocompleteValue(setAssociado, setAssociadoId, setIdUsuario, itensAssociado)}
                                inputValue={inputAssociado}
                                onInputChange={handleChangeAutocompleteValue(setInputAssociado, setInputAssociadoId)}
                                options={itensAssociado.map(value => value.usuarioECnpj)}
                                renderInput={handleRender}
                                size="small" 
                                />
                            )
                        }
                        {(selected === 'pecas') && (
                            <FormAutocomplete
                                titulo={'Peas'}
                                value={pecas}
                                onChange={handleChangeAutocompleteValue(setPecas, setPecasId, setItemId, itemsPecas)}
                                inputValue={inputPecas}
                                onInputChange={handleChangeAutocompleteValue(setInputPecas, setInputPecasId)}
                                options={itemsPecas.map(value => value.name)}
                                renderInput={handleRender}
                                size="small">
                            </FormAutocomplete>
                        )}
                        {(selected === 'instrucaoLimpeza') && (
                            <FormTextInputSemChild
                                titulo={"Instruo limpeza"}
                                value={instrucaoLimpeza}
                                onChange={handleChange(setInstrucaoLimpeza)} />
                        )}
                        {(selected === 'limpezaUtilizada') && (
                            <FormTextInputSemChild
                                titulo={"Limpeza utilizada"}
                                value={limpezaUtilizada}
                                onChange={handleChange(setLimpezaUtilizada)} />
                        )}
                        {(selected === 'danoReclamado') && (
                            <FormTextInputSemChild
                                titulo={"Dano reclamado"}
                                value={danoReclamado}
                                onChange={handleChange(setDanoReclamado)} />
                        )}
                        {(selected === 'dataOcorrencia') && (
                            <FormDateInputSemChild
                                titulo={"Data ocorrncia*"}
                                value={dataOcorrencia}
                                onChange={handleChange(setDataOcorrencia)} 
                                />
                        )}
                        {(selected === 'dataSaida') && (
                            <FormDateInputSemChild
                                titulo={"Data sada"}
                                value={dataSaida}
                                onChange={handleChange(setDataSaida)} 
                                />
                        )}
                        {(selected === 'marca') && (
                            <FormAutocomplete
                            titulo={'Marca'}
                            value={marca}
                            onChange={handleChangeAutocompleteValue(setMarca, setMarcaId, setBrandId, itemsMarca)}
                            inputValue={inputMarca}
                            onInputChange={handleChangeAutocompleteValue(setInputMarca, setInputMarcaId)}
                            options={itemsMarca.map(value => value.name)}
                            renderInput={handleRender}
                            size="small" />
                           
                        )}
                        {(selected === 'tipoFibra') && (
                            <FormTextInputSemChild
                                titulo={"Tipo de fibra*"}
                                value={tipoFibra}
                                onChange={handleChange(setTipoFibra)} 
                                />
                        )}
                        {(selected === 'foiPassado') && (
                            <FormRadioInputSemChild
                                titulo={"Artigo foi passado?*"}
                                value={foiPassado}
                                onChange={handleChange(setFoiPassado)} 
                                />
                        )}
                        {(selected === 'foiSecado') && (
                            <FormRadioInputSemChild
                                titulo={"Artigo foi passado?*"}
                                value={foiSecado}
                                onChange={handleChange(setFoiSecado)} 
                                />
                        )}
                        {(selected === 'encaminhaSeguro') && (
                            <FormRadioInputSemChild
                                titulo={"Artigo foi passado?*"}
                                value={encaminhaSeguro}
                                onChange={handleChange(setEncaminhaSeguro)} 
                                />
                        )}
                        {(selected === 'autorizaTestes') && (
                            <FormRadioInputSemChild
                                titulo={"Artigo foi passado?*"}
                                value={autorizaTestes}
                                onChange={handleChange(setAutorizaTestes)} 
                                />
                        )}
                        {(selected === 'estado') && (
                            <FormTextInputSemChild
                            titulo={"Estado"}
                            value={estado}
                            onChange={() => console.log('faz nada')}
                            />
                        )}
                        {(selected === 'cidade') && (
                            <FormTextInputSemChild
                            titulo={"Cidade"}
                            value={cidade}
                            onChange={() => console.log('faz nada')}
                            />
                        )}
                        {(selected === 'solucao') && (
                            <FormTextInputSemChild
                            titulo={"Soluo"}
                            value={solucao}
                            onChange={handleChange(setSolucao)}
                            />
                        )}
                        {(selected === 'responsabilidade') && (
                            <FormTextInputSemChild
                            titulo={"Responsabilidade"}
                            value={responsabilidade}
                            onChange={handleChange(setResponsabilidade)}
                            />
                        )}
                        {(selected === 'nomeTecnico') && (
                            <FormTextInputSemChild
                            titulo={"Nome do tcnico"}
                            value={nomeTecnico}
                            onChange={handleChange(setNomeTecnico)}
                            />
                        )}
                        {(selected === 'nomeCliente') && (
                            <FormTextInputSemChild
                            titulo={"Nome do cliente"}
                            value={nomeCliente}
                            onChange={handleChange(setNomeCliente)}
                            />
                        )}
                        {(selected === 'ticket') && (
                            <FormTextInputSemChild
                            titulo={"Ticket"}
                            value={ticket}
                            onChange={handleChange(setTicket)}
                            />
                        )}
                        {(selected === 'observacao') && (
                            <FormTextInputSemChild
                                titulo={"Observao"}
                                value={observacao}
                                onChange={handleChange(setObservacao)} 
                                />
                        )}
                        {(selected === 'encaminha') && (
                            <FormRadioInputSemChild
                                titulo={"Encaminhamento"}
                                value={encaminha}
                                onChange={handleChange(setEncaminha)} 
                                />
                        )}
  
                    </ModalEditItem>

                    <FormTextInput
                        titulo={"Associado*"}
                        value={associado}
                        disabled={true}
                        onChange={handleChange(setAssociado)}
                    >
                        <EditItem onClick={handleClick(setAssociado, setInputAssociado, 'associado', associado)} />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Peas*"}
                        value={pecas}
                        disabled={true}
                        onChange={handleChange(setPecas)}>
                            <EditItem onClick={handleClick(setPecas, setInputPecas, 'pecas', pecas)} /> 
                    </FormTextInput>
                
                    
                    <FormTextInput
                        titulo={"Instruo limpeza*"}
                        value={instrucaoLimpeza}
                        disabled={true}
                        onChange={handleChange(setInstrucaoLimpeza)}>
                            <EditItem onClick={handleClickFormText(setInstrucaoLimpeza, 'instrucaoLimpeza', instrucaoLimpeza)} /> 
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Limpeza utilizada*"}
                        value={limpezaUtilizada}
                        disabled={true}
                        onChange={handleChange(setLimpezaUtilizada)}>
                            <EditItem onClick={handleClickFormText(setLimpezaUtilizada, 'limpezaUtilizada', limpezaUtilizada)} /> 
                    </FormTextInput>
                    
                    <FormTextInput
                        titulo={"Dano reclamado*"}
                        value={danoReclamado}
                        disabled={true}
                        onChange={handleChange(setDanoReclamado)}>
                            <EditItem onClick={handleClickFormText(setDanoReclamado, 'danoReclamado', danoReclamado)} /> 
                    </FormTextInput>

                    <FormDateInput
                        titulo={"Data ocorrncia*"}
                        value={dataOcorrencia}
                        disabled={true}
                        onChange={handleChange(setDataOcorrencia)}>
                            <EditItem onClick={handleClickFormText(setDataOcorrencia, 'dataOcorrencia', dataOcorrencia)} /> 
                    </FormDateInput>

                    <FormDateInput
                        titulo={"Data sada*"}
                        value={dataSaida}
                        disabled={true}
                        onChange={handleChange(setDataSaida)}>
                            <EditItem onClick={handleClickFormText(setDataSaida, 'dataSaida', dataSaida)} /> 
                    </FormDateInput>
                    
                    <FormTextInput
                        titulo={"Marca*"}
                        value={marca}
                        disabled={true}
                        onChange={handleChange(setMarca)}>
                            <EditItem onClick={handleClick(setMarca, setInputMarca, 'marca', marca)} /> 
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Tipo de Fibra*"}
                        value={tipoFibra}
                        disabled={true}
                        onChange={handleChange(setTipoFibra)}>
                            <EditItem onClick={handleClickFormText(setTipoFibra, 'tipoFibra', tipoFibra)} /> 
                    </FormTextInput>

                    <FormRadioInput
                        titulo={"Artigo foi passado?"}
                        value={foiPassado}
                        disabled={true}
                        onChange={handleChange(setFoiPassado)}>
                        <EditItem onClick={handleClickFormText(setFoiPassado, 'foiPassado', foiPassado)} />
                    </FormRadioInput>
                    <FormRadioInput
                        titulo={"Artigo foi secado em secadora?"}
                        value={foiSecado}
                        disabled={true}
                        onChange={handleChange(setFoiSecado)}>
                        <EditItem onClick={handleClickFormText(setFoiSecado, 'foiSecado', foiSecado)} />
                    </FormRadioInput>
                    <FormRadioInput
                        titulo={"Artigo ser encaminhado para seguro?"}
                        value={encaminhaSeguro}
                        disabled={true}
                        onChange={handleChange(setEncaminhaSeguro)}>
                        <EditItem onClick={handleClickFormText(setEncaminhaSeguro, 'encaminhaSeguro', encaminhaSeguro)} />
                    </FormRadioInput>
                    <FormRadioInput
                        titulo={"Autoriza testes no artigo?"}
                        value={autorizaTestes}
                        disabled={true}
                        onChange={handleChange(setAutorizaTestes)}>
                        <EditItem onClick={handleClickFormText(setAutorizaTestes, 'autorizaTestes', autorizaTestes)} />
                    </FormRadioInput>
                    <FormTextInput
                        titulo={"Estado"}
                        disabled={true}
                        value={estado}
                        onChange={handleChange(setEstado)} 
                    >
                        <S.VoidIcon />
                    </FormTextInput>
                    <FormTextInput
                        titulo={"Cidade"}
                        disabled={true}
                        value={cidade}
                        onChange={handleChange(setCidade)} 
                    >
                        <S.VoidIcon />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Responsabilidade"}
                        value={responsabilidade}
                        disabled={true}
                        onChange={handleChange(setResponsabilidade)}>
                        <EditItem onClick={handleClickFormText(setResponsabilidade, 'responsabilidade', responsabilidade)} />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Tcnico"}
                        value={nomeTecnico}
                        disabled={true}
                        onChange={handleChange(setNomeTecnico)}>
                        <EditItem onClick={handleClickFormText(setNomeTecnico, 'nomeTecnico', nomeTecnico)} />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Nome do cliente*"}
                        value={nomeCliente}
                        disabled={true}
                        onChange={handleChange(setNomeCliente)}>
                        <EditItem onClick={handleClickFormText(setSolucao, 'nomeCliente', nomeCliente)} />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Ticket"}
                        value={ticket}
                        disabled={true}
                        onChange={handleChange(setTicket)}>
                        <EditItem onClick={handleClickFormText(setTicket, 'ticket', ticket)} />
                    </FormTextInput>

                    <FormTextInput
                        titulo={"Soluo"}
                        value={solucao}
                        disabled={true}
                        onChange={handleChange(setSolucao)}>
                            <EditItem onClick={handleClickFormText(setSolucao, 'solucao', solucao)} /> 
                    </FormTextInput>
                    <FormTextInput
                        titulo={"Observao"}
                        value={observacao}
                        disabled={true}
                        onChange={handleChange(setObservacao)}>
                            <EditItem onClick={handleClickFormText(setObservacao, 'observacao', observacao)} /> 
                    </FormTextInput>
                    <FormRadioInput
                        titulo={"Encaminhamento"}
                        value={encaminha}
                        disabled={true}
                        onChange={handleChange(setEncaminha)}>
                        <EditItem onClick={handleClickFormText(setEncaminha, 'encaminha', encaminha)} />
                    </FormRadioInput>
                    <FormSubmitButton titulo={"Salvar alteraes"} type="submit"/>
                </S.Formulario>
                )}
                
            </Paper>

            
            
        </S.ContainerMain>
    )
}