
import * as S from './style'
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';

export default function FormDateInput(props) {
    return (
        <S.ContainerItemCadastro>
            <S.ItemTitle>{props.titulo}</S.ItemTitle>
            <S.ContainerComButton>
                <Stack spacing={3} sx={{ width: "100%", maxWidth: "420px" }}>
                    <TextField
                        id="date"
                        type="date"
                        sx={{ width: '100%', maxWidth: "420px" }}
                        size="small"
                        disabled={props.disabled}
                        value={props.value}
                        onChange={props.onChange}
                    />
                </Stack>
                <S.ContainerIcon onClick={props.children.props.onClick}>
                    {props.children}
                </S.ContainerIcon>
            </S.ContainerComButton>
        </S.ContainerItemCadastro>
    )
}