import React from "react";
import { Routes, Route } from 'react-router-dom'

import Inicial from '../paginas/Inicial'
import Laudos from '../paginas/Laudos'
import Ocorrencias from '../paginas/Ocorrencias'
import Relatorios from '../paginas/Relatorios'
import Administradores from '../paginas/Administradores'
import Associados from '../paginas/Associados'
import Login from "../paginas/Login";
import SideBar from "../components/SideBar";
import CadastrarOcorrencia from "../paginas/CadastrarOcorrencia";
import EditarOcorrencia from "../paginas/EditarOcorrencia";

export default function Rotas() {
    return (
        <Routes>
            <Route exact path="/" element={<Login />} />
            <Route exact path="/login" element={<Login />} />
            <Route element={<SideBar/>}>
                <Route exact path="/inicial" element={<Inicial />} />
                <Route exact path="/info" element={<Laudos />} />
                <Route exact path="/ocorrencias" element={<Ocorrencias />} />
                <Route exact path="/dados-associados" element={<Associados />} />
                <Route exact path="/cadastrar-ocorrencia" element={<CadastrarOcorrencia />} />
                <Route exact path="/editar-ocorrencia" element={<EditarOcorrencia />} />
                <Route exact path="/relatorios" element={<Relatorios />} />
                <Route exact path="/administradores" element={<Administradores />} />
            </Route>
        </Routes>
    )
}