import * as React from 'react';
import DeleteIcon from '@mui/icons-material/Delete';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { useState } from 'react';
import { useEffect } from 'react';
import http from '../../http';
import { useNavigate } from 'react-router-dom';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    borderRadius: '5px',
    boxShadow: 24,
    p: 4,
  };

export default function DeleteItem(props) {
    const [open, setOpen] = React.useState(false);
    const [confirmaExclusaoBase, setConfirmaExcllusaoBase] = useState('')
    const navigate  = useNavigate()
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const confirmaDeletar = () => {
        http.delete(`http://localhost:3001/usuarios/${props.linha}`)
        .then(res => {
            if(res.status === 200) {
                alert(res.data.message)
                setOpen(false)
            }
        }).then(() => navigate('/ocorrencias'))
    }

    const cancela = () => {
        setOpen(false);
    }
    const handleClick = (event) => {
        setOpen(true);
    }
    return (
        <>
            <IconButton onClick={handleClick}>
                <DeleteIcon />
            </IconButton>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                        Deletar usurio - confirmao
                    </Typography>
                    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                        Deseja deletar usurio?
                    </Typography>

                    <Button onClick={confirmaDeletar}>Confirma</Button>
                    <Button onClick={cancela}>Cancela</Button>
                </Box>
            </Modal>
        </>
    )
}